.about-studio {
  padding: 34px 0 68px;
  background-color: var(--color-mine-shaft);

  &__container {
    gap: 0 30px;
  }

  &__subtitle {
    grid-column: 1 / 13;
    margin-bottom: 8px;
    color: var(--color-white);
  }

  &__text {
    grid-column: 1 / 7;
    grid-row: 2 / 5;
    line-height: 28px;
    color: var(--color-silver);
  }

  &__img {
    grid-column: 7 / 13;
    margin-bottom: 21px;
  }

  &__form {
    grid-column: 7 / 13;
  }
}

.newsletter {
  &__subtitle {
    margin-bottom: 15px;
    font-weight: 400;
    color: var(--color-white);
  }

  &__wrapper {
    display: flex;
    justify-content: space-between;
  }

  &__input {
    border: 0;
    padding: 16px 23px;
    width: 473px;
    color: var(--color-white);
    background-color: var(--color-tundora);

    &::placeholder {
      color: var(--color-silver);
    }
  }

  &__btn {
    padding: 16px 23px;
    color: var(--color-white);
    background-color: var(--color-orange);
  }
}
