.hero {
  margin-bottom: 29px;
}

.hero-left {
  position: relative;
  grid-column: 1 / 7;
  grid-row: 1 / 4;

  &__img {
    height: 100%;
    object-fit: cover;
  }

  &__tag {
    position: absolute;
    right: 0;
    top: 0;
    transform: translate(-15px, 15px);
  }

  &__text {
    position: absolute;
    left: 0;
    bottom: 0;
    transform: translate(59px, -64px);
  }

  &__subtitle {
    margin-bottom: 4px;
    font-weight: 400;
    font-size: 16px;
  }

  &__desc {
    font-family: "TTFirsNeue", sans-serif;
    font-weight: 700;
    font-size: 36px;
    line-height: 36px;

    &--second {
      margin-bottom: 20px;
    }
  }

  &__link {
    display: inline-block;
    padding: 16px 22.5px;
    font-family: "Roboto", sans-serif;
    font-weight: 700;
    background-color: var(--color-orange);
  }
}

.hero-right {
  &__subtitle {
    grid-column: 7 / 13;
  }

  &__img {
    &--first {
      grid-column: 7 / 13;
      grid-row: 2 / 3;
    }

    &--second {
      grid-column: 7 / 10;
      grid-row: 3 / 4;
    }

    &--third {
      grid-column: 10 / 13;
      grid-row: 3 / 4;
    }
  }
}

.tag {
  padding: 7px 15px;
  font-family: "Roboto", sans-serif;
  font-size: 14px;
  background-color: var(--color-mine-shaft2);
}

.subtitle {
  font-family: "TTFirsNeue", sans-serif;
  font-weight: 700;
  font-size: 24px;
  line-height: 24px;
  color: var(--color-mine-shaft);
}
